import './App.css';
import {BrowserRouter as Router, Routes, Route} from "react-router-dom"
import Home from './Pages/Home';
import About from './Pages/About';
import Calculator from './Pages/Calculator';
import Error from './Pages/Error';
import Navbar from './Pages/Navbar';
import Si from './Pages/Si';

function App() {
  return (
    <Router>
        <Navbar title="Calculator"/>
      <Routes>
        <Route path="/calculateit.com/calculator" element={<Calculator/>}></Route>
        <Route path="/calculateit.com" element={<Home/>}></Route>
        <Route path="/calculateit.com/about" element={<About/>}></Route>
        <Route path="/calculateit.com/si_calculator" element={<Si/>}></Route>
        <Route path="*" element={<Error/>}></Route>
      </Routes>
    </Router>
  );
}

export default App;
