import React from "react";
import {Link} from "react-router-dom"

export default function Navbar(props) {

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/calculateit.com">
          {props.title}
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link " aria-current="page" to="/calculateit.com">
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link " aria-current="page" to="/calculateit.com/calculator">
                Calculator
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link " aria-current="page" to="/calculateit.com/si_calculator">
                SI Calculator
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link " to="/calculateit.com/about">
                About
              </Link>
            </li>
          </ul>
          <form className="d-flex">
            <input
              className="form-control me-2"
              type="search"
              placeholder="Search"
              aria-label="Search"
              
            />
            <button className="btn btn-outline-success enabled" type="submit">
              Search
            </button>
          </form>
        </div>
      </div>
    </nav>
  );
}
