import React from 'react';
import "./App1.css";
import { useState } from 'react';

export default function Excalculator() {
    const [calc, setCalc] = useState("");
    const [result, setresult] = useState("");
  
    const ops = ['/', '*', '+', '-', '.'];
  
    const updateCalc = value =>{
      if (
        ops.includes(value) && calc === '' ||
        ops.includes(value) && ops.includes(calc.slice(-1)
        ) 
      ) {
        return;
      }
  
      setCalc(calc + value);
  
      if (!ops.includes(value)){
        const upp = setresult(eval(calc + value).toString());
      }
    }
  
  
    const createDigits = () => {
        const digits = [];
  
        for (let i = 1; i < 10; i++) {
          digits.push(
            <button 
            onClick={(cursors) => updateCalc(i.toString())} 
            key={i}>
              {i}
              </button>
  
          )
        }
  
        return digits;
    }
  
    const calculate = () => {
      const sub = setCalc(eval(calc).toString());
    }
  
    const deleteLast = () =>{
      if (calc == ''){
        return;
      }
      const value = calc.slice(0, -1);
      const sub = calc.slice(0, -1);
      const upp = calc.slice(0, -1);
  
      setCalc(value);
      setCalc(sub);
      setCalc(upp);
    }
    const cls = () =>{
      if (calc == ''){
        return;
      }
      const value = '';
      const sub = '';
      const upp = '';
  
      setCalc(value);
      setCalc(sub);
      setCalc(upp);
    }
    const [cursorX, setCursorX] = useState();
    const [cursorY, setCursorY] = useState();

    window.addEventListener('mousemove', (e) =>{
      setCursorX(e.pageX)
      setCursorY(e.pageY)
    })

  return (
    <>
        <div className="App">
      <div className="calculator">
        <div className="display"> 
          {calc || "0"}
        </div>
        <div className="operators">
          <button onClick={() => updateCalc('/')}></button>
          <button onClick={() => updateCalc('*')}>x</button>
          <button onClick={() => updateCalc('+')}>+</button>
          <button onClick={() => updateCalc('-')}>-</button>
        </div>

        <div className="conn">
        <div className="digits">
          { createDigits() }
          <button onClick={() => updateCalc('0')}>0</button>
          <button onClick={() => updateCalc('.')}>.</button>
          <button onClick={() => updateCalc('**')}>^</button>


        </div>
        <button onClick={calculate} className="equalto">=</button>

        </div>
        <div className="clear">
        <button onClick={deleteLast} className="delete">DELETE</button>
        <button onClick={cls} className="cls">CLS</button>
        </div>
      </div>
    </div>
    </>
  )
}


