import React from "react";
import "./Si.css";


export default function Exsi() {

    const calcu = () => {
        let p = document.getElementById("amount").value;
        let r = document.getElementById("intrest").value;
        let t = document.getElementById("time").value;
        let d = (p*r*t)/100
        document.getElementById("displayi").innerText = d;
    }
    const cleart = () => {
        document.getElementById("amount").value = "";
        document.getElementById("intrest").value = "";
        document.getElementById("time").value = "";
        document.getElementById("displayi").innerText = "";
    }
  return (
    <div className="appi">
      <div className="heading">SI Calculator</div>

      <body>
        <div className="calculatori">
          <div className="displayi" id="displayi"></div>
          Enter Amount:
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <input type="text" name="amount" id="amount" className="" />
          <br></br>
          <br></br>
          Enter Rate of Intrest:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{" "}
          <input type="text" name="intrest" id="intrest" className="" />
          <br></br>
          <br></br>
          Enter the period of Time:&nbsp;{" "}
          <input type="text" name="time" id="time" className="" />
          <br></br> <br></br>
          <div className="buttoncon">
          <button className="" onClick={calcu}>Calculate</button>&nbsp;&nbsp;&nbsp;
          <button className="" onClick={cleart}>Clear</button>
          </div>
        </div>
      </body>
    </div>
  );
}
